import {createRouter, createWebHashHistory} from 'vue-router'
//pages components
import Home from '../components/Home.vue'
import AboutPage from '../components/AboutPage.vue'
import CampamentosVeranoPage from '../components/CampamentosVeranoPage.vue'
import SurvivorPage from '../components/SurvivorPage.vue'
import ExcursionesPage from '../components/ExcursionesPage.vue'

const routes = [
    { path: '/', component: Home },
    { path: '/quienes-somos', component: AboutPage },
    { path: '/servicios/campamento-verano', component: CampamentosVeranoPage },
    { path: '/servicios/survivor-gerencial', component: SurvivorPage },
    { path: '/servicios/excursiones', component: ExcursionesPage },
 ]
 

 const router = createRouter({
   // 4. Provide the history implementation to use. We are using the hash history for simplicity here.
   history: createWebHashHistory(),
   routes, // short for `routes: routes`
   scrollBehavior () {
    // always scroll to top
   return { top: 0 }
   }
 })

 export {router}