<template>
  <div class="breadcrumb container-fluid">
    <div class="container">
    <p class="breadcrumb__text text-left"><b>Estás en:</b> {{path}}</p>
    </div>
  </div>
</template>

<script>
export default {
    name: "Breadcrumb",
    data(){
        return{
         path: ""
        }
    },
    mounted(){
      let path = this.$router.currentRoute.value.fullPath;
      this.path= path.match(/(?<=\/+)\w*-*\w*/ig).map(el=>el.replace("-", " ")).join(" / ");
    }
};
</script>

<style lang="scss">
@import "../../consts/colors.scss";
.breadcrumb{
    background-color: $bg-light;
    border-top: 1px solid #f0f0f0;
    border-bottom: 1px solid #f0f0f0;
    color: $primary-light-font;
    &__text{
      padding-top: 20px;
    }
}
</style>