
class MenuManager{
    constructor(name= "root"){
        this.name = name;
        this.menu = [];
    }

    addLink(name, path){
       this.menu.push({name,path})
    }
    addChild(name){
      let child =  new MenuManager(name);
      this.menu.push(child);
      return child;
    }

    getMenu(){
       return this.menu.map(el=>{
           if(el.path) return el;
           else return {name: el.name, child: el.menu}
       })
    }

}

let menuManager = new MenuManager();
menuManager.addLink("Inicio","/")
menuManager.addLink("Quienes somos","/quienes-somos")
let servicioslink = menuManager.addChild("Servicios")
servicioslink.addLink("Campamentos de Verano",'/servicios/campamento-verano');
servicioslink.addLink("Survivor Gerencial",'/servicios/survivor-gerencial');
servicioslink.addLink("Excursiones y Actividades",'/servicios/excursiones');

const menuLinks = menuManager.getMenu();

export default menuLinks;
