<template>
  <nav
    class="nav-menu-left-panel"
    v-bind:class="{
      'nav-menu-left-panel--toggled-in': leftBarToggled,
      'nav-menu-left-panel--toggled-out': !leftBarToggled,
    }"
    v-on:click="closeLeftBar()"
  >
    <div
      v-for="link in menuLinks"
      :key="link.name"
      class="nav-menu-left-panel__item"
    >
      <router-link
        v-if="link.path"
        v-bind:to="link.path"
        class="nav-menu-left-panel__link-lv1"
        >{{ link.name }}</router-link
      >
      <span v-if="link.child" class="nav-menu-left-panel__label-lv2">
        {{ link.name }} <font-awesome-icon :icon="['fas', 'angle-down']"
      /></span>
      <router-link
        v-for="child in link.child"
        :key="child.name"
        v-bind:to="child.path"
        class="nav-menu-left-panel__link-lv2"
        >{{ child.name }}</router-link
      >
    </div>
  </nav>
  <nav
    id="nav-menu"
    class="nav-menu"
    v-bind:class="{
      'nav-menu--maximized': scrollPosition < 300,
      'nav-menu--minimized': scrollPosition >= 300,
    }"
  >
    <div class="app-container nav-menu-container">
      <div class="nav-menu__brand">
        <a href="#/"
          ><img
            src="../assets/logo.png"
            class="img-fluid"
            alt="Imagen logo ECA"
        /></a>
      </div>
      <div class="nav-menu__actions">
        <div v-for="link in menuLinks" :key="link.name" class="nav-menu__item">
          <router-link
            v-if="link.path != undefined"
            v-on:click="setActiveParentMenu(link.name)"
            v-bind:to="link.path"
            class="nav-menu__link"
            >{{ link.name }}</router-link
          >
          <div
            v-if="link.child != undefined"
            class="dropdown nav-menu__dropdown"
          >
            <a
              class="nav-menu__link"
              v-bind:class="{
                'router-link-active':
                  parentMenuActive == link.name.toLowerCase(),
              }"
              href="#"
              id="navbarDropdownMenuLink"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              {{ link.name }}
              <font-awesome-icon
                class="nav-menu__icon"
                :icon="['fas', 'angle-down']"
              />
            </a>
            <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
              <router-link
                v-for="child in link.child"
                :key="child.name"
                class="dropdown-item nav-menu__link-lv2"
                v-bind:to="child.path"
                >{{ child.name }}</router-link
              >
            </div>
          </div>
        </div>
      </div>
      <label for="toggle-btn" class="--hidden-accesibility">Left panel Toggle button</label>
      <button id="toggle-btn" class="nav-menu__toggle-btn" v-on:click="toggleLeftBarHandler">
        <font-awesome-icon :icon="['fas', 'bars']" />
      </button>
    </div>
  </nav>
</template>

<script>
import menuLinks from "../consts/menuMap";

export default {
  name: "Navbar",
  data() {
    return {
      leftBarToggled: false,
      menuLinks,
      scrollPosition: 0,
      parentMenuActive: "",
    };
  },
  methods: {
    setActiveParentMenu(parentname) {
      parentname = parentname.toLowerCase();
      if (this.parentMenuActive !== parentname) {
        this.parentMenuActive = parentname;
      }
    },
    toggleLeftBarHandler() {
      this.leftBarToggled = !this.leftBarToggled;
    },
    closeLeftBar() {
      this.leftBarToggled = false;
    },
    scrollHandler() {
      let scrollYposition = document.getElementsByTagName("body")[0].scrollTop;
      console.log(scrollYposition);
    },
  },
  watch: {
    $route() {
      this.closeLeftBar();
      //watch router changes to make lv1 menu patent active
      let path = this.$router.currentRoute.value.fullPath;
      let parent = path.match(/(?<=\/+)\w*-*\w*/gi)[0];
      this.setActiveParentMenu(parent);
    },
  },
  mounted() {
    window.addEventListener("scroll", () => {
      var st = window.pageYOffset || document.documentElement.scrollTop;
      this.scrollPosition = st;
    });
  },
};
</script>

<style lang="scss">
//aniemation dos not work in scoped style
//TODO: convert to scoped
//source : https://stackoverflow.com/questions/47283989/why-are-css-keyframe-animations-broken-in-vue-components-with-scoped-styling
@import "../consts/colors.scss";
.nav-menu-left-panel {
  display: none;
}

.nav-menu-container {
  width: 75vw;
  min-width: 750px;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  min-height: 20px;
  align-items: stretch;
}

.nav-menu {
  z-index: 999999;
  position: fixed;
  top: 0%;
  min-width: 100%;
  background-color: white;
  max-height: 100px;
  height: 100px;
  box-shadow: 0 -3px 12px;
  &__icon {
    position: relative;
    top: 2px;
    left: 2px;
  }
  // &--maximized{
  //   animation: maximized 1s forwards;
  // }
  //  @keyframes maximized {
  //    0%{
  //       height: 50px;
  //    }
  //   100%{
  //     height: 100pxpx;
  //   }
  // }
  // &--minimized{
  //   animation: minimized 1s forwards;
  // }
  // @keyframes minimized {
  //    0%{
  //       height: 100pxpx;
  //    }
  //   100%{
  //     height: 50px;
  //   }
  // }

  &__toggle-btn {
    display: none;
  }
  &__brand {
    margin-top: 5px;
  }

  &__actions {
    display: flex;
    flex-direction: row;
    margin-right: 50px;
  }
  &__dropdown {
    position: relative;
    display: block;
    height: 100%;
  }
  .router-link-active {
    color: $primary;
    border-bottom: 3px solid $primary;
  }
  &__link {
    color: $primary-light-font;
    text-decoration: none;
    height: 100px;
    display: block;
    padding-top: 30px;
    margin-right: 30px;
    text-align: center;
    &:hover {
      color: $primary;
      border-bottom: 3px solid $primary;
      text-decoration: none;
    }
  }
  &__link-lv2 {
    font-size: 0.8em;
    color: $primary-light-font;
    &:hover {
      color: $primary !important;
      background-color: transparent !important;
    }
  }
}

@media (max-width: 500px) {
  .nav-menu-container {
    width: 100%;
    min-width: 100%;
  }

  .nav-menu {
    height: 50px;
    padding: 10px;
    &__brand {
      display: block;
      width: 60px;
    }
    &__actions {
      display: none;
    }
    &__toggle-btn {
      display: block;
      margin-left: 10px;
      border: none;
      background-color: transparent;
      flex-wrap: nowrap;
      &:hover {
        opacity: 0.4;
        cursor: pointer;
      }
    }
  }
  .nav-menu-left-panel {
    z-index: 999998;
    position: fixed;
    top: 0;
    background-color: $color-jet;
    color: white;
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    height: 100%;
    max-width: 50vw;
    justify-content: left;
    &--toggled-in {
      display: flex;
      animation: in-animation;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
    }
    &--toggled-out {
      display: flex;
      animation: out-animation;
      animation-duration: 0.4s;
      animation-fill-mode: forwards;
    }
    &__item {
      display: flex;
      flex-direction: column;
    }
    &::before {
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      min-width: 100vw;
      min-height: 100%;
    }

    &__link-lv1 {
      text-decoration: none;
      color: white;
      text-align: left;
      padding-left: 30px;
      padding-bottom: 10px;
      &:hover {
        text-decoration: none;
        color: white;
        opacity: 0.6;
      }
    }
    &__link-lv2 {
      text-decoration: none;
      color: white;
      text-align: left;
      font-size: 0.85em;
      padding-left: 40px;
      padding-right: 10px;
      padding-bottom: 10px;
      padding-top: 10px;
      background-color: $color-old-lavender;
      &:hover {
        text-decoration: none;
        color: white;
        opacity: 0.6;
      }
    }
    &__label-lv2 {
      padding-left: 30px;
      padding-right: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
    }
  }
  //animations
  @keyframes in-animation {
    0% {
      left: -110%;
    }
    100% {
      left: 0px;
    }
  }
  @keyframes out-animation {
    0% {
      left: 0;
    }
    100% {
      left: -110%;
    }
  }
}
</style>